import { createClient } from "@/lib/supabase/server"
import { notFound } from "next/navigation"
import { Button } from "@/components/ui/button"
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle
} from "@/components/ui/card"
import { Copy } from "lucide-react"
import { Badge } from "@/components/ui/badge"

async function getGame(supabase: ReturnType<typeof createClient>, gameId: string) {
  const { data: game, error } = await supabase
    .from("games")
    .select("*, host:profiles(*)")
    .eq("id", gameId)
    .single()
  if (error) {
    console.error("Error fetching game:", error)
    notFound()
  }
  return game
}

async function getGamePlayers(
  supabase: ReturnType<typeof createClient>,
  gameId: string
) {
  const { data: players, error } = await supabase
    .from("game_players")
    .select("*, profile:profiles(*)")
    .eq("game_id", gameId)
  if (error) {
    console.error("Error fetching players:", error)
    return []
  }
  return players
}

export default async function GamePage({
  params
}: {
  params: { gameId: string }
}) {
  const supabase = createClient()
  const { gameId } = params
  const game = await getGame(supabase, gameId)
  const players = await getGamePlayers(supabase, gameId)

  return (
    <div className="container mx-auto max-w-2xl py-8">
      <Card>
        <CardHeader>
          <CardTitle>{game.description}</CardTitle>
          <CardDescription>
            Hosted by {game.host?.display_name ?? "..."}
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-6">
          <div className="space-y-2">
            <h3 className="font-semibold">Game ID</h3>
            <div className="flex items-center gap-2">
              <p className="text-muted-foreground font-mono text-lg">
                {game.short_code}
              </p>
              {/* TODO: Add copy to clipboard functionality */}
              <Button variant="outline" size="icon">
                <Copy className="h-4 w-4" />
              </Button>
            </div>
            <p className="text-muted-foreground text-sm">
              Share this ID with others to let them join the game.
            </p>
          </div>

          <div className="space-y-2">
            <h3 className="font-semibold">Players</h3>
            <ul className="space-y-2">
              {players.map((player) => (
                <li
                  key={player.user_id}
                  className="flex items-center justify-between rounded-md border p-3"
                >
                  <span className="font-medium">
                    {player.profile?.display_name ?? "..."}
                  </span>
                  <Badge
                    variant={
                      player.status === "approved"
                        ? "default"
                        : player.status === "pending"
                          ? "secondary"
                          : "destructive"
                    }
                  >
                    {player.status}
                  </Badge>
                </li>
              ))}
            </ul>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}